</template>
<script>
import { dataRequest as api } from '@/api/api'
export default {
	name: 'HeaDer',
	components: {
		// 组件
	},

	data() {
		return {
			// 參數
			arclist: '',
			list: '',
		}
	},
	methods: {
		//函數
		async getdata() {
			// 正序 還是順序
			// 根據哪個表排序 這個好像 之需要
			let array = await api(['post=getarticlepage', { perPage: 4, page: 1, offset: 'DESC', tosort: 'popularity', thumbnail: 'hasValue' }])

			array = array.data
			this.arclist = array
			for (const key in this.arclist) {
				this.arclist[key].Creationtime = this.arclist[key].Creationtime.substr(0, 10)
			}
			array = await api(['post=getarticlepage', { perPage: 10, page: 1, offset: 'DESC', tosort: 'popularity', thumbnail: 'noValue' }])

			array = array.data
			this.list = array
			for (const key in this.list) {
				this.list[key].Creationtime = this.list[key].Creationtime.substr(0, 10)
			}
		},
	},
	mounted() {
		this.getdata()
	},
}
</script>
<style scoped>
