<template>
	<div>
		<div id="player-container">
			<div class="watermark"></div>
		</div>
		<button @click="playVideo">播放视频{{ jianting }}</button>
	</div>
</template>
<script>
import { ref, onMounted, watch } from 'vue'
export default {
	name: 'MyVideoPlayer',
	props: ['medium'],

	data() {
		return {
			player: null,
			videoWidth: 860,
			jianting: '',
		}
	},

	methods: {
		playVideo() {
			if (this.player) {
				// this.player.play()
				console.log(this.player)
				this.jianting = 'gaibian'
			}
		},
	},
	mounted() {
		// 初始化阿里云播放器
		//player.alicdn.com/video/editor.mp4
		console.log(this.medium)
		this.player = new window.Aliplayer({
			id: 'player-container',
			source: this.medium,
			autoplay: true,
			width: '100%',
			height: `${this.videoHeight}px`,
			isLive: false,
			autoPlayDelay: 3,
			autoPlayDelayDisplayText: '请等待...',
			// controlBarVisibility: 'never', // 设置控制栏不可见
			// useH5Prism: false, // 不使用H5播放器，避免字幕按钮出现
		})
		// 监听视频播放完毕事件
		this.player.on('ended', this.handleVideoEnded)
	},
	computed: {
		videoHeight() {
			const videoAspectRatio = 16 / 9 // 假设视频宽高比例是 16:9
			return this.videoWidth / videoAspectRatio
		},
	},
	watch: {
		jianting(newVal, oldVal) {
			// 在 jianting 值改变时触发的回调函数
			console.log('jianting 值改变:', newVal, oldVal)
		},
	},
}
</script>
<style>
#player-container {
	position: relative;
}

.watermark {
	position: absolute;
	top: 10px;
	right: 10px;
	width: 100px;
	height: 50px;
	opacity: 0.7;
	/* 设置水印图片的背景 */
	background: url('./../assets/logo.svg'); /* 替换成水印图片的路径 */
	/* 这里可以继续添加其他样式，比如文字水印等 */
}
</style>
