import { createStore } from 'vuex';

export default createStore({
  state: {
    logon: false,
    user: null, // Initialize user as null or with default values
  },
  mutations: {
    setLogon(state, value) {
      state.logon = value;
    },
    setUser(state, user) {
      state.user = user;
    },
  },
  getters: {},
  actions: {},
  modules: {},
});