<template>
	<main v-my-directive data-title="问答社区" v-loading.fullscreen.lock="loading">
		<div class="QAHead">
			<div class="questionnav line">
				<div class="columnLayout interval">
					<div class="center QAfont Horizontalspacing">
						<span>问答社区</span>
						<div class="iconfont icon-dian center maxfont bold fonfff"></div>
						<span class="fonfff">有问必答</span>
					</div>
					<div class="Horizontalspacing interval fonfff">
						<div class="Horizontalspacing minxgap">
							<div class="iconfont icon-duigouxiao fonfff minfont center"></div>
							<div>学习技能、解决问题</div>
						</div>
						<div class="Horizontalspacing minxgap">
							<div class="iconfont icon-duigouxiao fonfff minfont center"></div>
							<div>学习技能、解决问题</div>
						</div>
						<div class="Horizontalspacing minxgap">
							<div class="iconfont icon-duigouxiao fonfff minfont center"></div>
							<div>学习技能、解决问题</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="main">
			<div>
				<div class="content">
					<div id="box" class="Genericpadding Horizontalspacing interval">
						<div class="QAgreybox" @click="getqa()">全部问题</div>
						<div class="QAgreybox" v-for="(item, index) in nav" :key="index" @click="getqa(item.alias)">{{ item.name }}</div>
					</div>
					<!--  -->
					<div class="Genericpadding columnLayout">
						<div class="QArectangle articlebt">
							<div class="Horizontalspacing interval">
								<div class="qzsolld" :class="{ 'qzsolldav Green': tesorting == 0 }" @click=";(tesorting = 0), getqa()">最新问题</div>
								<!-- <div class="qzsolld" v-for="(item, index) in nav" :key="index">{{ item }}</div> -->
								<div class="qzsolld" :class="{ 'qzsolldav Green': tesorting == 1 }" @click=";(tesorting = 1), getqa()">人气问题</div>
								<!-- 回答为0的 -->
								<div class="qzsolld" :class="{ 'qzsolldav Green': tesorting == 2 }" @click=";(tesorting = 2), getqa()">等你来答</div>
								<div class="qzsolld" :class="{ 'qzsolldav Green': tesorting == 3 }" @click=";(tesorting = 3), getqa()">已有回答</div>
								<div class="qzsolld" :class="{ 'qzsolldav Green': tesorting == 4 }" @click=";(tesorting = 4), getqa()">悬赏问题</div>
							</div>
							<router-link to="askaquestion"
								><div class="qzbuttons"> <div>+</div><span>提交问题</span> </div></router-link
							>
						</div>
						<ul class="columnLayout">
							<li v-for="(item, index) in qalist.slice(index, index + pagesize)" :key="index" class="qzlist qzlistgrid minxgap">
								<div :class="[item.reply > 0 ? 'Green' : 'grey', item.reply > 0 ? 'QAbox' : 'QAboxno']">
									<div class="columnLayout"
										><span class="center">{{ item.reply }}</span
										><span class="center">回答</span>
									</div>
								</div>
								<div class="columnLayout space-around">
									<router-link :to="'/qaarticle/' + item.alias + '/' + item.id + '.html'"
										><div class="turngreen">{{ item.title }}</div></router-link
									>
									<div class="articlebt minfont grey line">
										<div class="Horizontalspacing minaxgap">
											<div class="Horizontalspacing minixgap">
												<img class="pimgmins" src="./../assets/img/avatar.png" alt="" />
												<span class="center">{{ item.username }}</span>
											</div>
											<div class="center">{{ item.pidname }}</div>
											<div class="Horizontalspacing minixgap">
												<div class="iconfont icon-riqi_o"></div>
												<span class="center">{{ item.Creationtime }}</span>
											</div>
											<div class="Horizontalspacing minixgap">
												<div class="iconfont icon-icon"></div>
												<span class="center">{{ item.PageView }}</span>
											</div>
											<div class="Horizontalspacing minixgap" v-if="item.Price > 0">
												<div class="iconfont icon-dashangcishu red"></div>
												<span class="center Horizontalspacing">
													<p>悬赏</p>
													<p class="red">{{ item.Price }}</p>
													<p>元</p>
													<p v-if="item.Bestresponse">已解决</p>
												</span>
											</div>
										</div>
										<!-- 有最佳答案才显示 -->
										<router-link class="center" :to="'/qaarticle/' + item.alias + '/' + item.id + '.html'" v-if="item.optimal">问题有人回答啦，进来学习一下吧~</router-link>
										<router-link class="center Illanswer" :to="'/qaarticle/' + item.alias + '/' + item.id + '.html'" v-else>我来回答</router-link>
									</div>
								</div>
							</li>
							<!--  -->
						</ul>
						<el-pagination background class="centerend" :hide-on-single-page="value" @current-change="fun" :current-page="curPage" :page-size="pagesize" layout="prev, pager, next" :total="total"> </el-pagination>
					</div>
				</div>
			</div>
			<div>
				<qaleft></qaleft>
			</div>
		</div>
	</main>
</template>
<script>
import qaleft from './../components/qaleft'
import { dataRequest as api, setText } from '@/api/api'
export default {
	name: 'name',
	components: {
		// 组件
		qaleft,
	},
	data() {
		return {
			//返回值
			loading: true,
			nav: [],
			alias: '',
			qalist: [],
			order: false,
			tesorting: '0',
			grey: 'grey',
			Green: 'Green',
			QAbox: 'QAbox',
			QAboxno: 'QAboxno',
			value: true,

			pagesize: 10, //每页显示数量
			curPage: 1, // 当前页数
			total: 0, // 一共几条数据
			index: 0, //开始页码
		}
	},
	methods: {
		// 函数
		async get() {
			// 获取分类
			let nav = await api(['post=getqacat', ''])
			if (nav.code == 200) {
				this.nav = nav.data
				this.getqa()
			}
		},
		// 获取问题
		async getqa(ele = '') {
			if (ele) {
				this.alias = ele
				// 获取某个分类
			} else {
				this.alias = ''
			}
			let array = await api(['post=getquestions', { alias: this.alias }])
			if (array.code == 200) {
				array = array.data
				console.log(array)
				for (let i = 0; i < array.length; i++) {
					array[i].reply = array[i].reply.length
					array[i].Price = (Math.round(array[i].Price * 1000) / 1000).toString()
				}
				this.qalist = array
				// console.log(this.qalist)
				this.Updatesorting()
			}
		},
		// 更新排序
		Updatesorting() {
			//顺序切换
			this.order = !this.order

			this.qalist = this.qalist.sort((a, b) => {
				//
				return new Date(b.Creationtime).getTime() / 1000 - new Date(a.Creationtime).getTime() / 1000
			})
			if (this.tesorting == 0) {
				this.qalist = this.qalist.sort((a, b) => {
					//
					if (!this.order) {
						return new Date(a.Creationtime).getTime() / 1000 - new Date(b.Creationtime).getTime() / 1000
					}
				})
			} else if (this.tesorting == 1) {
				this.qalist = this.qalist.sort((a, b) => {
					//
					if (!this.order) {
						return new Date(a.PageView).getTime() / 1000 - new Date(b.PageView).getTime() / 1000
					} else {
						return new Date(b.PageView).getTime() / 1000 - new Date(a.PageView).getTime() / 1000
					}
				})
			} else if (this.tesorting == 2) {
				this.qalist = this.qalist.filter(ele => {
					return !ele.optimal
				})
			} else if (this.tesorting == 3) {
				this.qalist = this.qalist.filter(ele => {
					return ele.optimal
				})
			} else if (this.tesorting == 4) {
				this.qalist = this.qalist.filter(ele => {
					return ele.Price > 0
				})
			}
			this.total = this.qalist.length
			this.fun()
		},
		fun(ele = 1) {
			this.curPage = ele
			this.index = (this.curPage - 1) * this.pagesize
			window.scrollTo({ behavior: 'smooth', top: 10 })
			let arr = []

			for (let i = this.index; i < this.index + this.pagesize && i < this.qalist.length; i++) {
				arr.push(this.qalist[i].id)
			}
			api(['post=QAbrowsing', arr])
		},
	},
	mounted() {
		// 生命周期:
		this.get()
	},
	updated() {
		this.loading = false
	},
}
</script>
