import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import index from '../views/index.vue'
import article from '../views/article.vue'
import column from '../views/column.vue'
import homepage from '../views/homepage.vue'
import question from '../views/question.vue'
import qaarticle from '../views/qaarticle.vue'
import askaquestion from '../views/askaquestion.vue'
// 用户中心
import user from '../user/index.vue'
import MyCollection from '../user/MyCollection.vue'

import privateletter  from '../user/privateletter.vue'
import forum from '../user/forum.vue'
import myquestion from '../user/question.vue'
import comment from '../user/comment.vue'
import course from '../user/course.vue'
import order from '../user/order.vue'
import settings from '../user/settings.vue'

const routes: Array<RouteRecordRaw> = [
  // 固定的不能搓
  {
    path: '/',
    name: 'index',
    component: index,
    meta:{
      title:'首页'
    }
  },
  {
    path: '/article/:comn/:articid',
    name: 'article',
    component: article,
    meta:{
      title:'文章内容'
    }
  },
  {
    path: '/column/:comn',
    name: 'column',
    component: column,
    meta:{
      title:'栏目'
    }
  }, {
    path: '/:cathchAll(.*)',
    name: '错误',
    component: index,
    meta:{
      title:'404'
    }
  }
  ,
  {
    path: '/homepage',
    name: 'homepage',
    component: homepage,
    meta:{
      title:'个人主页'
    }
  }
  ,
  {
    path: '/question',
    name: 'question',
    component: question,
    meta:{
      title:'问答社区'
    }
  }
  ,
  {
    path: '/qaarticle/:comn/:index',
    name: 'qaarticle',
    component: qaarticle,
    meta:{
      title:'问题'
    }
  }
  ,
  {
    path: '/askaquestion',
    name: 'askaquestion',
    component: askaquestion,
    meta:{
      title:'发起提问'
    }
  }
  ,
  {
    path: '/user',
    name: 'user',
    component: user,
    meta:{
      title:'我的账户'
    }
  } ,
  {
    path: '/user/course',
    name: 'course',
    component: course,
    meta:{
      title:'我的课程'
    }
  }
  ,
  {
    path: '/user/order',
    name: 'order',
    component: order,
    meta:{
      title:'我的订单'
    }
  }
  ,
  {
    path: '/user/MyCollection',
    name: 'MyCollection',
    component: MyCollection,
    meta:{
      title:'我的收藏'
    }
  }
  ,
  {
    path: '/user/forum',
    name: 'forum',
    component: forum,
    meta:{
      title:'我的帖子'
    }
  }
  ,
  {
    path: '/user/question',
    name: 'myquestion',
    component: myquestion,
    meta:{
      title:'我的提问'
    }
  }
  ,
  {
    path: '/user/settings',
    name: 'settings',
    component: settings,
    meta:{
      title:'个人资料'
    }
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
