<template>
	<div v-if="!logon" class="LoginRegistration bold">
		<!-- 如果没有登陆显示 -->
		<div class="center" @click=";(logonbox = true), (homelogon = true)">登录</div>
		<div class="center" @click=";(logonbox = true), (register = true)">注册</div>
	</div>
	<!-- 登录后 -->
	<div v-else class="Afterloggingin positioned center">
		<div class="Headcircle">
			<el-avatar class="Roundheadportrait" :size="60" v-if="user" :src="user.Avatar">
				<div class="image-slot iconfont icon-setting-role"></div>
			</el-avatar>
			<el-avatar v-else class="Roundheadportrait" :size="60">
				<div class="image-slot iconfont icon-setting-role"></div>
			</el-avatar>
			<!-- <img src="./../assets/img/avatar.png" alt="" class="Roundheadportrait" /> -->
			<div class="iconfont icon-renzhengguanli authentication YellowLabelCertification"></div>
		</div>
		<!-- Genericpadding 宽度要重新定义 -->
		<div class="Popupafterlogin">
			<div class="Popupafterlogins columnLayout interval">
				<div class="dropdown-box"></div>
				<div class="dropdown-boxr"></div>
				<div class="Horizontalspacing minaxgap zindex">
					<div class="Headcircle">
						<el-avatar class="Roundheadportrait" :size="60" v-if="user" :src="user.Avatar">
							<div class="image-slot iconfont icon-setting-role"></div>
						</el-avatar>
						<el-avatar v-else class="Roundheadportrait" :size="60">
							<div class="image-slot iconfont icon-setting-role"></div>
						</el-avatar>
						<!-- <img src="./../assets/img/avatar.png" alt="" class="Roundheadportrait" />  -->
					</div>
					<span class="center">{{ user.username }}</span
					><span class="center Whitebox minifont Green padding5">{{ user.name }}</span>
				</div>
				<div class="Whitebox padding10 grey minfont articlebt">
					<div class="Horizontalspacing minxgap"><span>余额:</span><span>0.0</span></div>
					<div class="Horizontalspacing minxgap"><span>消费:</span><span>0.0</span></div>
				</div>
				<div class="userbuttons zindex">
					<span class="center">{{ $t('message.PersonalCenter') }}</span>
				</div>
				<div class="Whitebox padding20 grey minfont Horizontalspacing space-around zindex">
					<a class="turngreen" href="http://" target="_blank" rel="noopener noreferrer">主页</a>
					<a class="turngreen" href="http://" target="_blank" rel="noopener noreferrer">私信</a>
					<a class="turngreen" href="http://" target="_blank" rel="noopener noreferrer">资料</a>
					<p class="turngreen" @click="exit">退出</p>
				</div>
			</div>
		</div>
	</div>
	<!-- 登录 点击隐藏当前  -->
	<div v-if="logonbox && !logon" @click.self=";(logonbox = false), (homelogon = false), (register = false), (Retrievepassword = false), (Retrievepasswordverification = false), (resetpassword = false)" class="logonbox">
		<div id="logonmanbox" class="center logonmanbox columnLayout interval">
			<div class="columnLayout minaxgap"><img src="./../assets/logo.svg" alt="" class="logonimg center" /><span class="center">Hi~欢迎登录 DEXC区块链技术培训学院</span></div>
			<form v-if="homelogon" id="homelogon" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-yonghu grey"></div><input class="Searchinput" type="text" v-model="account" placeholder="请输入您的用户名或邮箱账号" /> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><el-input class="denglucss" placeholder="请输入密码" v-model="password" show-password></el-input> </div>
				<div class="Horizontalspacing space-around minfont">
					<div class="Horizontalspacing minxgap"><span class="grey">没有账号?</span><span @click=";(register = true), (homelogon = false)">立即注册!</span></div
					><span @click=";(Retrievepassword = true), (homelogon = false)">忘记密码?</span>
				</div>
				<div class="loginbutton" @click="login">登录</div>
			</form>
			<!-- 注册 -->
			<!-- 获取默认用户组 -->

			<form autocomplete="off" v-if="register" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-yonghu grey"></div><input class="Searchinput" name="register" data-name="username" type="text" placeholder="请输入账号" /> </div>

				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><el-input placeholder="请输入密码" @input="fun" v-model="input" show-password></el-input> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><el-input placeholder="请再次输入密码" v-model="input" show-password></el-input> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-youxiang grey"></div><input class="Searchinput" name="register" data-name="email" type="mail" placeholder="请输入您的邮箱" /> </div>
				<div class="loginbutton" onclick="register()">注册</div>
				<div class="minfont center">
					<div class="Horizontalspacing minxgap"><span class="grey">已有账号?</span><span @click=";(homelogon = true), (register = false)">立即登录!</span></div>
				</div>
			</form>
			<!-- 忘记密码  -->
			<form id="Retrievepassword" v-if="Retrievepassword" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-yonghu grey"></div><input class="Searchinput" type="text" placeholder="请输入您的用户名" /> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-youxiang grey"></div><input class="Searchinput" name="Retrieve" data-name="email" type="mail" placeholder="请输入您的邮箱" /> </div>
				<div class="loginbutton" @click=";(Retrievepassword = false), (Retrievepasswordverification = true)">下一步</div>
			</form>
			<!-- 验证验证码 -->
			<form id="Retrievepasswordverification" v-if="Retrievepasswordverification" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><input class="Searchinput" type="text" placeholder="验证码" /> </div>
				<div class="loginbutton" @click=";(Retrievepasswordverification = false), (resetpassword = true)">下一步</div>
			</form>
			<!-- 重设密码 -->
			<form id="resetpassword" v-if="resetpassword" autocomplete="off" class="columnLayout interval paddingtop10">
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><input class="Searchinput" name="Retrieve" data-name="password" type="password" placeholder="请输入密码" autocomplete="new-password" /> </div>
				<div class="loginsearchinput Horizontalspacingg minxgap"> <div class="iconfont icon-tianchongxing- grey"></div><input class="Searchinput" name="Retrieve" data-name="Confirm" type="password" placeholder="请再次输入密码" autocomplete="new-password" /> </div>
				<!-- 这里确定就可以直接登录了 -->
				<div class="loginbutton">修改密码</div>
			</form>
		</div>
	</div>
</template>
<script>
import { mapState } from 'vuex'
import { dataRequest as api, automaticLogin, Logoutoflogin } from '@/api/api'
import sha256 from 'crypto-js/sha256'
export default {
	name: 'name',
	computed: {
		...mapState(['logon', 'user']),
	},
	components: {
		// 组件
	},
	data() {
		return {
			// 账号密码
			account: '',
			password: '',
			//返回值
			logonbox: false,
			// 登录
			homelogon: false,
			// 注册
			register: false,
			// 忘记密码
			Retrievepassword: false,
			Retrievepasswordverification: false,
			resetpassword: false,
			//返回值
		}
	},
	methods: {
		// 函数
		//登陆成功获取JWT  然后 在这里存入数据  然后去请求登陆接口即可
		//后续每次请求都带上JWT
		async login() {
			if (this.account.replace(/\s/g, '') && this.password.replace(/\s/g, '')) {
				// let arr = await logonapi(this.account, sha256(sha256(this.password)).toString())
				let arr = await api(['post=JWT', { username: this.account, password: sha256(sha256(this.password)).toString() }])

				// 这里传递账号和密码给后端
				console.log(arr)
				if (arr.code == 200) {
					// 登陆成功

					// 将JWT存在数据库
					const data = { id: 1, JWT: arr.data, logon: false }

					if (await this.$indexedDB.getObject(data.id)) {
						// 存在修改
						this.$indexedDB.updateObject(data)
					} else {
						// 不存在 添加
						this.$indexedDB.addObject(data)
					}
					// 登录
					await automaticLogin()
					this.logonbox = false
				} else {
					this.$message.error('账号或密码错误')
				}
				//发送全局消息
			} else {
				this.$message.error('账号和密码不能为空')
				console.log(this.logon)
			}
			// 每次都去尝试登陆
		},
		async automaticlogin() {
			let arr = await automaticLogin()
		},
		// 退出登陆
		async exit() {
			Logoutoflogin()
		},
	},
	mounted() {
		// 修改登陆 也就是修改存储数据的地方  建议是将数据存在本地数据库
		// 存储账号 密码 登陆状态
		// 这里自动登陆
		this.automaticlogin()
	},
}
</script>
<style>
.denglucss .el-input__wrapper {
	height: 16px;
	border: none !important;

	box-shadow: none !important;
}
</style>
